:root {
  --color-dark-3xl: #292929;
  --color-dark-2xl: #444444;
  --color-dark-xl: #4c4c4c;
  --color-dark-l: #545454;
  --color-dark-m: #b2b2b2;
  --color-dark-s: #cdcdcd;
  --color-light-s: #fafafa;
  --color-light-l: #f6f6f6;
  --color-light-xl: #ffffff;
  --color-primary: #f1cdb3;
  --color-primary-light: #fddcc4;
  --fz-nav-link: 15px;
  --lh-nav-link: 24px;
  --fz-strong-text: 44px;
  --lh-strong-text: 57px;
  --fz-section-text: 15px;
  --lh-section-text: 24px;
  --fz-section-title: 35px;
  --lh-section-title: 130%;
  --fz-button: 17px;
  --lh-button: 22px;
  --fz-main-text: 20px;
  --lh-main-text: 23px;
}

$laptop-width: 1280px;
$tablet-width: 768px;

@media (max-width: $tablet-width) {
  :root {
    --fz-nav-link: 32px;
    --lh-nav-link: 51px;
  }
}
